import React, { Component } from 'react';
import { connect } from 'react-redux';






class ApiCall extends Component {


  render() {

    const {response} = this.props;
    return (
        <div style={{padding: '100px'}}>
       {
        response && <div> "there is a response"</div>
       }


      
      </div>
    );
  }
}



const mapStateToProps = ({api_reduc}) => {
  return{

     response: api_reduc.response,
    
    };
};

const mapDispatchToProps = dispatch => ({
  fetch: (url, data, config) =>dispatch({ type: "API_CALL_REQUEST", url: url, data: data, config: config})
});





export default connect(mapStateToProps, mapDispatchToProps) (ApiCall);



