
const SUBMIT_ADDRESS = "SUBMIT_ADDRESS";
const BACK_TO_ADDRESS = "BACK_TO_ADDRESS";


const initialState={
	
	data: {name: 'Joe Consumer', line1: '', state: '', postcode: '', countryCode: 'US', phoneNumber: '212000000'},
	submitted: false,

	

}
export const address = (state = initialState, action)=>{
	switch(action.type){

	case SUBMIT_ADDRESS:
		return {...state, data: action.data, submitted: true}

	case BACK_TO_ADDRESS:
		return {...state, submitted: false}
	
	default :
		return state;
	}
}




