import React, { Component } from 'react';
import { connect } from 'react-redux';
import AddressInput from './AddressInput';
import PaymentInput from './PaymentInput';
import ApiCall from './ApiCall';
import {composeAPIData} from './helpers';
import { API_CONFIG} from './constants/API_CONFIG.js'
import './App.css';
const API_URL = "http://api.us-sandbox.afterpay.com/v1/";


class App extends Component {

  componentDidUpdate(prevProps){
    if (this.props.payment_submitted !== prevProps.payment_submitted){

      this.props.fetch(API_URL,this.props.API_DATA, API_CONFIG);
    }
  }


  render() {

     const { fetching, payment_submitted, address_submitted, error } = this.props;

    
    return (
      <div className="App">
        <header className="App-header">
        
          <h1 className="App-title">Internal Payment Gateway</h1>
        </header>
        <div className="row">

          <div className="col-md-4">
          
          </div>

          <div className="col-md-4">
           
            {fetching? (<div> Making API REQUEST... </div> ) : payment_submitted ? (<ApiCall />) : address_submitted? (<div> <PaymentInput/></div>) : (<div> <AddressInput/> </div>)}

            {error && <p  style={{ color: "red", padding: "50px" }}>Uh oh - something went wrong! Please check the console for further information.</p>}

         </div>

         <div className="col-md-4">
             
          
          </div>

        </div>

      </div>
    );
  }
}
const mapStateToProps = ({address, payment, api_reduc}) => {
  return{
    fetching: api_reduc.fetching,
     
      error: api_reduc.error,
      
      
      payment_submitted: payment.submitted,
      address_submitted: address.submitted,
      API_DATA: composeAPIData(address.data, payment.data),
    
    };
};

const mapDispatchToProps = dispatch => ({
  fetch: (url, data, config) =>dispatch({ type: "API_CALL_REQUEST", url: url, data: data, config: config})
});



export default connect(mapStateToProps, mapDispatchToProps)(App);



