import React, { Component } from 'react';
import PropTypes from "prop-types";
import Button from '@material-ui/core/Button';
import { ValidatorForm, TextValidator} from 'react-material-ui-form-validator';
import MenuItem from "@material-ui/core/MenuItem";
import Select from "@material-ui/core/Select";
import Input from "@material-ui/core/Input";
import { connect } from 'react-redux';

class PaymentInput extends Component {

  constructor(props){
    super(props);
    


    this.state = {
      data: props.payment_data,
    }


  }

  dispatchSubmitPayment = () => {


 this.props.submit_payment(this.state.data);


}

handleChange = (event) =>{

  const {data} = this.state;
  data[event.target.name] = event.target.value;



  this.setState({data});

}
  render() {

    const {data} = this.state;
    return (
        <div style={{padding: '100px'}}>
       

        <ValidatorForm
                ref="form"
                onSubmit={this.dispatchSubmitPayment}
                onError={errors => console.log(errors)}
            >

            <div className="row">

              <Select
                value={data["payment_method"]}
                onChange={this.handleChange}
                
                input={
                  <Input name="payment_method" disableUnderline />
                      }
                >
                <MenuItem value="Credit Card"><em>Credit Card</em></MenuItem>
                <MenuItem value="Checking Account">Checking Account</MenuItem>
                <MenuItem value="Paypal">Paypal</MenuItem>
                <MenuItem value="Another Option">Another Option</MenuItem>
                
              </Select>
             </div>
            { this.state.data['payment_method'] === "Credit Card" && 
            <div id="creditCard">
              <div className="row">

                <TextValidator
                        label="Credit Card Number"
                        onChange={this.handleChange}
                        name="card_number"
                        value={data['card_number']}
                        validators={['required', 'matchRegexp:^4[0-9]{12}(?:[0-9]{3})?$' ]}
                        errorMessages={['this field is required','Please enter a valid Visa Card Number. ']}
                    />
               </div>
               <div className="row">

                <TextValidator
                        label="CVV Code"
                        onChange={this.handleChange}
                        name="cvv"
                        value={data['cvv']}
                        validators={['required','isNumber', 'minNumber: 0', 'maxNumber:999']}
                        errorMessages={['this field is required', 'Please enter a 3 or 4 digit number','Please enter a 3 or 4 digit number', 'Please enter a 3 or 4 digit number']}
                    />
               </div>
               <div className="row">

                <TextValidator
                        label=" Expiration Date"
                        onChange={this.handleChange}
                        name="date"
                        value={data['date']}
                        validators={['required']}
                        errorMessages={['this field is required']}
                    />
               </div>
              </div>
            }
             <div className="row">
             <Button style={{marginTop: "25px"}} onClick={() => this.props.go_back()}>Go Back</Button>
            <Button style={{marginTop: "25px"}} type="submit ">Complete Order</Button>
            
            </div>

         </ValidatorForm>
      </div>
    );
  }
}

const mapStateToProps = ({payment}) =>{
  return {
    payment_data: payment.data,
  }
}

const mapDispatchToProps = dispatch => ({
  submit_payment: (payment_data) => dispatch({type: "SUBMIT_PAYMENT", data: payment_data}),
  go_back: () => dispatch({type: "BACK_TO_ADDRESS"}),
 
})

PaymentInput.propTypes = {
  payment_data: PropTypes.object.isRequired,
  submit_payment: PropTypes.func.isRequired,
  go_back: PropTypes.func.isRequired,
}




export default connect(mapStateToProps, mapDispatchToProps) (PaymentInput);



