import React, { Component } from 'react';
import PropTypes from "prop-types";

import Button from '@material-ui/core/Button';
import { ValidatorForm, TextValidator} from 'react-material-ui-form-validator';
import { connect } from 'react-redux';




class AddressInput extends Component {

  constructor(props){
    super(props);
    


    this.state = {
      data: props.address_data,
    }
  }

  dispatchSubmitAddress = () => {

 this.props.submit_address(this.state.data);
}

handleChange = (event) =>{

  const {data} = this.state;
  data[event.target.name] = event.target.value;



  this.setState({data});

}
  render() {

    const {data} = this.state;
    return (
        <div style={{padding: '100px'}}>
       

        <ValidatorForm
                ref="form"
                onSubmit={this.dispatchSubmitAddress}
                onError={errors => console.log(errors)}
            >
             <div className="row">

              <TextValidator
                      label="Address Line 1"
                      classes={{}}
                      onChange={this.handleChange}
                      name="line1"
                      value={data['line1']}
                      validators={['required']}
                      errorMessages={['this field is required']}
                  />
             </div>
             <div className="row">

              <TextValidator
                      label="State"
                      onChange={this.handleChange}
                      name="state"
                      value={data['state']}
                      validators={['required']}
                      errorMessages={['this field is required']}
                  />
             </div>
             <div className="row">

              <TextValidator
                      label="Zip Code"
                      onChange={this.handleChange}
                      name="postcode"
                      value={data['postcode']}
                      validators={['required']}
                      errorMessages={['this field is required']}
                  />
             </div>
             <div className="row">
            <Button style={{margin: "25px"}} type="submit ">Next Step</Button>
            </div>

         </ValidatorForm>
      </div>
    );
  }
}

const mapStateToProps = ({address}) =>{
  return {
    address_data: address.data,
  }
}

const mapDispatchToProps = dispatch => ({
  submit_address: (address_data) => dispatch({type: "SUBMIT_ADDRESS", data: address_data})
})

AddressInput.propTypes = {
  address_data: PropTypes.object.isRequired,
  submit_address: PropTypes.func.isRequired,
}


export default connect(mapStateToProps, mapDispatchToProps) (AddressInput);



